@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;500;600;700&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary-color: #2BD25B;
  --secondary-color: #1B67B2;
  --text-color: #333333;
  --background-color: #F7F9FC;
  --card-background: #FFFFFF;
  --border-color: #E0E0E0;
  --success-color: #2BD25B;
  --warning-color: #F39C12;
  --danger-color: #E74C3C;
  --info-color: #3498DB;
}

body {
  margin: 0;
  font-family: 'Nunito', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-color);
  color: var(--text-color);
}

.dark-mode {
  --primary-color: #3DE76B;
  --secondary-color: #3D85D1;
  --text-color: #E0E0E0;
  --background-color: #1A2A3A;
  --card-background: #253545;
  --border-color: #3A4A5A;
  --success-color: #3DE76B;
  --warning-color: #FFAD33;
  --danger-color: #FF5F5F;
  --info-color: #5CAFEE;
}

/* Custom component styles using @apply */
@layer components {
  .btn {
    @apply px-4 py-2 rounded-md font-semibold transition-all duration-200 shadow-sm;
  }
  
  .btn-primary {
    @apply bg-portnox-primary text-white hover:bg-opacity-90 active:bg-opacity-100;
  }
  
  .btn-secondary {
    @apply bg-portnox-secondary text-white hover:bg-opacity-90 active:bg-opacity-100;
  }
  
  .btn-outline {
    @apply border border-gray-300 text-gray-700 hover:bg-gray-100 dark:text-gray-300 dark:border-gray-600 dark:hover:bg-gray-700;
  }
  
  .dashboard-card {
    @apply bg-white dark:bg-gray-800 p-5 rounded-lg shadow-card transition-all duration-300 hover:shadow-hover;
  }
  
  .chart-container {
    @apply bg-white dark:bg-gray-800 p-5 rounded-lg shadow-card my-4 overflow-hidden;
  }

  .metric-value {
    @apply text-2xl md:text-3xl font-bold;
  }

  .highlight-value {
    @apply text-portnox-primary dark:text-portnox-primary;
  }

  .form-control {
    @apply w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-portnox-primary focus:border-transparent;
  }

  .sidebar-item {
    @apply flex items-center space-x-2 px-4 py-2.5 rounded-md transition-colors;
  }

  .sidebar-item.active {
    @apply bg-portnox-primary bg-opacity-10 text-portnox-primary;
  }
}

/* Print styles for financial report */
@media print {
  /* Hide non-report elements */
  header, footer, nav, .sidebar, .stakeholder-tabs, .action-buttons, .tab-panel, button {
    display: none !important;
  }
  
  /* Adjust main content */
  .content-area {
    padding: 0 !important;
    margin: 0 !important;
    width: 100% !important;
  }
  
  /* Adjust report container */
  .financial-report {
    padding: 0 !important;
    margin: 0 !important;
  }
  
  /* Ensure proper colors */
  body {
    background-color: white !important;
    color: black !important;
    font-size: 12pt;
  }
  
  /* Fix tables */
  table {
    page-break-inside: auto !important;
    break-inside: auto !important;
    font-size: 10pt;
  }
  
  tr {
    page-break-inside: avoid !important;
    break-inside: avoid !important;
    page-break-after: auto !important;
    break-after: auto !important;
  }
  
  td, th {
    page-break-inside: avoid !important;
    break-inside: avoid !important;
  }
  
  /* Page breaks */
  h2 {
    page-break-before: auto !important;
    break-before: auto !important;
    page-break-after: avoid !important;
    break-after: avoid !important;
    margin-top: 20pt !important;
  }
  
  /* Remove shadows and effects that don't print well */
  .shadow-sm, .shadow, .shadow-md, .shadow-lg, .shadow-xl {
    box-shadow: none !important;
  }
  
  /* Logo and header styles */
  img {
    max-width: 100% !important;
    page-break-inside: avoid !important;
  }
  
  /* Add page numbers */
  @page {
    margin: 1cm;
  }
  
  /* Background colors in print */
  .bg-green-50, .bg-blue-50, .bg-purple-50, .bg-gray-50 {
    background-color: white !important;
    border: 1px solid #ddd !important;
  }
}

