.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-content {
  display: flex;
  flex: 1 1;
  position: relative;
}

.content-area {
  flex: 1 1;
  overflow-y: auto;
  padding: 1rem;
}

@media (max-width: 1023px) {
  .sidebar {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    z-index: 50;
  }
}

